<template>
  <div>
    <van-overlay :show="loadingBtn" :z-index="222">
      <van-loading class="loading" size="24px" vertical color="#0094ff">
        加载中...
      </van-loading>
    </van-overlay>
    <van-action-sheet
      class="confirm-location"
      v-model="showFlag"
      title="请确认上车位置"
      @closed="handleClosed"
    >
      <div class="content">
        <div class="text">
          <div class="title">您的位置:</div>
          <div class="address">
            {{ yourLocation.address }}
          </div>
        </div>
        <van-button class="confirm-btn" type="warning" @click="submitLocation"
          >确认无误</van-button
        >
      </div>
    </van-action-sheet>
  </div>
</template>

<script>
export default {
  name: "ConfirmLocation",
  props: {
    showConfirmLocationFlag: {
      type: Boolean,
      required: true,
    },
    yourLocation: {
      type: Object,
      required: true,
    },
    guid: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
      loadingBtn: false,
    };
  },
  computed: {
    showFlag: {
      get() {
        return this.showConfirmLocationFlag;
      },
      set(value) {
        this.$emit("update:showConfirmLocationFlag", value);
      },
    },
  },
  beforeRouteEnter(to, from, next) {
    // console.log(to, from ,next)
    // const {
    //   query: { guid },
    // } = to;
    // const reg =
    //   /[a-z0-9]{5,20}-[a-z0-9]{3,10}-[a-z0-9]{3,10}-[a-z0-9]{3,10}-[a-z0-9]{5,20}/i;
    // if (!reg.test(guid)) {
    //   next({
    //     path: "/error",
    //     query: { type: "0" },
    //   });
    // }
    // next(async (vm) => {
    //   vm.guid = guid;
    //   // await self.getFlag(guid);
    // });
  },
  methods: {
    //关闭弹窗
    handleClosed() {
      this.showFlag = false;
    },
    //提交位置
    async submitLocation() {
      //校验位置是否合法
      if (!this.yourLocation.lat) {
        this.$notify({
          type: "warning",
          message: "请选择正确的位置",
          duration: 3000,
        });
        return;
      }
      this.loadingBtn = true;
      this.$emit("update:showConfirmLocationFlag", false);
      try {
        const {
          address,
          lat,
          lng,
          addressComponent: { city, district, province, street, adcode },
        } = this.yourLocation;
        const wgsLocation = this.$locationUtils.gcj02Towgs84(
          lng,
          lat
        ); //高德坐标转wgs84
        const res = await this.$http({
          url: "AppRouteService.asmx/addLocation",
          method: "get",
          params: {
            locationJson: {
              address,
              latitude: wgsLocation.lat,
              longitude: wgsLocation.lng,
              city,
              district,
              province,
              street,
              adcode, //编码
              guid: this.guid,
              sematic_description: "",
              phone: "", //联系电话
            },
          },
        });
        this.$notify({
          type: "primary",
          message: "位置提交成功",
        });
        this.$emit("submitSuccess");

        this.loadingBtn = false;
      } catch (error) {
        console.log(error);

        this.$notify({
          type: "danger",
          message: "位置提交失败,请检查网络状态",
        });
        this.loadingBtn = false;
      }
    },
  },
};
</script>

<style scoped>
.confirm-location {
  height: 40%;
}
.content {
  display: flex;
  /* height: 100%; */
  /* height: 150px; */
  flex-direction: column;
  justify-content: space-evenly;
  /* align-items: space-around; */
  /* padding: 16px; */
  margin: 0 16px;
  /* margin-bottom: 0; */
}

.content .text {
  display: flex;
  flex-direction: row;
  /* justify-content: space-evenly; */
  align-items: center;
  text-align: left;
  margin: 20px 0;
}

.content .text .title {
  /* width: 100px; */
  flex: 1;
  padding-right: 10px;
  font-weight: 700;
}
.content .text .address {
  flex: 3;
  font-size: 14px;
}

.confirm-btn {
  margin: 40px 0;
  background-color: #3d4460;
  border-color: #3d4460;
  color: #fff;
}

.loading {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
</style>