<template>
  <van-action-sheet
    position="bottom"
    @closed="handleClosed"
    v-model="showFlag"
    :closeable="false"
    :title="title"
    class="search-sheet"
  >
    <div class="content">
      <div class="search-box">
        <van-search
          v-model="searchText"
          placeholder="请输入搜索关键词"
          input-align="left"
          @input="handleSearch"
          @cancel="handleCancel"
        />
      </div>
      <div class="cell-group">
        <van-cell-group v-if="searchAreaList.length">
          <van-cell
            v-for="item in searchAreaList"
            :key="item.id"
            :label="item.address"
            :value="item.distance+'米'"
            :title="item.name"
            label-class="text-left"
            title-class="text-left"
            center
            @click.native="selectLocation(item)"
          >
          </van-cell>
        </van-cell-group>
        <van-cell-group v-else>
          <van-cell
            v-for="item in surroundings"
            :key="item.id"
            :label="item.address"
            :value="item.distance+'米'"
            :title="item.name"
            label-class="text-left"
            title-class="text-left"
            center
            @click.native="selectLocation(item)"
          >
          </van-cell>
        </van-cell-group>
      </div>
    </div>
  </van-action-sheet>
</template>

<script>
export default {
  name: "SearchSheet",
  props: {
    showSearchSheetFlag: {
      type: Boolean,
      required: true,
    },
    surroundings: {
      type: Array,
      default() {
        return [];
      },
    },
    searchAreaList: {
      type: Array,
      default() {
        return [];
      },
    },
    title: {
      type: String,
      default: "请选择",
    },
  },
  computed: {
    showFlag: {
      get() {
        return this.showSearchSheetFlag;
      },
      set(value) {
        this.$emit("update:showSearchSheetFlag", value);
      },
    },
  },
  data() {
    return {
      searchText: "",
    };
  },
  methods: {
    handleClosed() {
      this.showFlag = false;
    },
    //搜索处理
    handleSearch(val) {
      console.log(val);
      this.$emit("search-input", val);
    },
    handleCancel() {
      console.log("取消");
    },
    //提交位置
    selectLocation(location) {
      // console.log(
      //   location.title,
      //   location.address,
      //   location.point.lat,
      //   location.point.lng
      // );

      this.$emit("select-location", location);
    },
  },
};
</script>

<style scoped>
.search-sheet {
  height: 75%;
  z-index: 100000;
}

/* 弹出层 */
.content {
  /* position: relative; */
  padding: 0px 16px 20px;
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  overflow: hidden;
}

.content div.search-box {
}
.content div.cell-group {
  max-height: 320px !important;
  overflow: scroll;
}

.text-left {
  text-align: left;
}
</style>